form {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

label {
  color: #6e6d70;
  text-align: center;
  max-width: 100%;
  height: 50px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .4));
  border: 3px solid #6e6d70;
  border-radius: 5px;
  margin-right: 10px;
  padding: 8px;
  font-size: medium;
}

button {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background: #6e6d70;
  border: none;
  border-radius: 6px;
  padding: 7px 16px;
  font-size: 30px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

button:hover, button:focus {
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

